:root {
  /* rainbow colors */
  --soy-2030: #f80c12;
  --soy-2029: #ee1100;
  --soy-2028: #ff3311;
  --soy-2027: #ff4422;
  --soy-2026: #ff6644;
  --soy-2025: #ff9933;
  --soy-2024: #feae2d;
  --soy-2023: #ccbb33;
  --soy-2022: #d0c310;
  --soy-2021: #aacc22;
  --soy-2020: #69d025;
  --soy-2019: #22ccaa;
  --soy-2018: #12bdb9;
  --soy-2017: #11aabb;
  --soy-2016: #4444dd;
  --soy-2015: #3311bb;
  --soy-2014: #3b0cbd;
  --soy-2013: #442299;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', 'Open Sans', Tahoma, Geneva, Verdana, sans-serif;
}

/* Icon button */
.icon-btn-action {
  cursor: pointer;
}
.icon-btn-edit {
  color: #2199f9;
}
.icon-btn-delete {
  color: red;
}

html,
body,
#root,
.App {
  height: 100%;
}

a {
  color: #2199f9;
  text-decoration: none;
}
